* {
  margin: 0;
  padding: 0;
}

.logo img {
  width: 8rem;
  height: 7rem;
}

body {
  background-color: #07192f;
}

.typewriter {
  color: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.nav-scroll {
  background-color: #07192f;
  z-index: 2 !important;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed !important;
  width: 100%;
  padding: 1rem;
}

.nav {
  z-index: 2 !important;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed !important;
  width: 100%;
  padding: 1rem;
}

.navList {
  display: flex;
  font-size: larger;
  list-style: none;
  justify-content: space-between;
}

.aboutContainer, .projectContainer, .contactContainer {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  padding-top: 10rem;
  height: 800px;
  width: 100%;
}

h2 {
  font-size: 2rem;
  font-weight: 900;
  color: white;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: center;
}

.introduction { 
  text-align: left !important;
}
 li {
  cursor: pointer;
  margin: 1rem;
  padding-bottom: .5rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: white;
}

li:hover {
  border-bottom: 2px solid lightblue;
}

h1 {
  color: white;
  font-size: 3rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 900;
}

#main {
  position: relative;
  height: 100vh;
  width: 100vw;
}

